import { Button } from "./button";
import { Avatar, AvatarFallback, AvatarImage } from "./avatar";

export default function Header() {
    return (
        <header className="w-full flex items-center justify-between px-6 py-4 bg-background border-b border-border shadow-sm">
            {/* Left: Two rounded-full buttons */}
            <div className="flex gap-2">
                <Button className="rounded-full px-4">Map</Button>
                <Button className="rounded-full px-4">Stats</Button>
            </div>
            {/* Center: App Name */}
            <div className="text-2xl font-bold tracking-widest select-none">DISPATCH</div>
            {/* Right: User name and avatar */}
            <div className="flex items-center gap-3">
                <span className="font-medium text-sm text-right">Navneeth</span>
                <Avatar>
                    <AvatarImage src="" alt="User" />
                    <AvatarFallback>N</AvatarFallback>
                </Avatar>
            </div>
        </header>
    );
} 